import React from 'react';
import {ReactComponent as DarkModeIcon} from './icons/darkmode.svg';

function DarkMode(props) {

    const handleClick = () => {
        // documentElement is <html>
        document.documentElement.classList.toggle('dark')
    }

    return (
        <div tabIndex={props.tabIndex} className={`${props.className} active:bg-gray-100 dark:active:bg-gray-500`} onClick={handleClick}>
            <DarkModeIcon />
        </div>
    )
}

export default DarkMode;