/**
 * On implementing dropdown menu: https://ncoughlin.com/posts/react-dropdown/
 */
import React, {useState, useEffect, useRef} from "react";
import {ReactComponent as ControlCentreIcon} from './icons/controlcentre.svg';
import {ReactComponent as WiFiIcon} from './icons/wifi.svg';
import {ReactComponent as BluetoothIcon} from './icons/bluetooth.svg';
import {ReactComponent as LocationIcon} from './icons/location.svg';
import {ReactComponent as BatteryIcon} from './icons/battery.svg';
import {ReactComponent as AirplaneModeIcon} from './icons/airplanemode.svg';
import {ReactComponent as DoNotDisturbIcon} from './icons/donotdisturb.svg';
import {ReactComponent as VolumeIcon} from './icons/volume.svg';
import {ReactComponent as BrightnessIcon} from './icons/brightness.svg'; 

function ControlCentre(props) {
    const [visible, setVisible] = useState(false);
    // create a ref variable, add it to the top <div> of the dropdown menu, i.e <div ref={ref}>
    const ref = useRef();

    useEffect(() => {
        const onBodyClick = e => {
            // if the any of the children of <div ref={ref}> is clicked, do nothing
            // ref.current: avoiding ref is null error
            if (ref.current && ref.current.contains(e.target)){
                return
            }
            // else, toggle the menu
            setVisible(false)
        }

        document.body.addEventListener('click', onBodyClick);
        
        // to avoid ref.current is null error
        return function cleanup() {
            document.body.removeEventListener('click', onBodyClick);
        }
    }, [])

    const tileClassName = "bg-gray-200 dark:bg-gray-700 shadow-lg rounded"

    return (
        <div ref={ref}>
            <div tabIndex={props.tabIndex} className={`${props.className} ${visible && 'bg-gray-100 dark:bg-gray-500' }`} 
                onClick={() => setVisible(!visible)}
            >
                <ControlCentreIcon />
            </div>

            {visible && (
            <div className="absolute my-0.5 top-8 right-0.5 rounded-lg bg-gray-300 dark:bg-black shadow-xl">

                <div className='flex my-3'>
                    <div className={`flex flex-col px-4 py-1 mr-1.5 ml-3 ${tileClassName} justify-center`}>
                        <div className='flex mb-3'>
                            <div className='mr-4'>
                                {/* Wi-Fi */}
                                <WiFiIcon />
                            </div>
                            <div className='ml-4'>
                                Wi-Fi
                            </div>
                        </div>
                        <div className='flex my-3'>
                            <div className='mr-4'>
                                {/* Bluetooth */}
                                <BluetoothIcon />
                            </div>
                            <div className='ml-4'>
                                Bluetooth
                            </div>
                        </div>
                        <div className='flex mt-3'>
                            <div className='mr-4'>
                                {/* Location */}
                                <LocationIcon />
                            </div>
                            <div className='ml-4'>
                                Location
                            </div>
                        </div>
                    </div>

                    <div className='flex flex-col ml-1.5 mr-3'>
                        <div className={`flex px-2 py-4 ${tileClassName} mb-1`}>
                            {/* Battery */}
                            <div className='mx-4'>
                                <BatteryIcon />
                            </div>
                            <div className='mx-4'>
                                100%
                            </div>
                        </div>

                        <div className="flex pt-2 justify-between text-sm">
                            <div className={`w-1/2 mr-1.5 px-2 py-2 flex flex-col items-center text-center ${tileClassName}`}>
                                <div className="my-1">
                                    {/* Airplane Mode */}
                                    <AirplaneModeIcon />
                                </div>
                                <div>
                                    <p>Airplane</p>
                                    <p>Mode</p>
                                </div>
                            </div>
                            <div className={`w-1/2 ml-1.5 px-2 py-2 flex flex-col items-center text-center ${tileClassName}`}>
                                <div className="my-1">
                                    {/* Do Not Disturb */}
                                    <DoNotDisturbIcon />
                                </div>
                                <div>
                                    <p>Do Not</p>
                                    <p>Disturb</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <div className={`flex mx-3 my-3 ${tileClassName} px-4 py-2 justify-between`}>
                    <VolumeIcon />
                    <input  
                        className="w-5/6"
                        type="range" 
                        min="0" max="10" 
                        step="1"/>
                </div>

                <div className={`flex mx-3 my-3 ${tileClassName} px-4 py-2 justify-between`}>
                    <BrightnessIcon />
                    <input 
                        className="w-5/6"
                        type="range" 
                        min="0" max="10" 
                        step="1"/>
                </div> 
            </div>
            )}
        </div>
    )
}

export default ControlCentre;